import React, { useEffect, useState, cloneElement } from 'react';
import { css } from '@emotion/react';
import { HeroButtons } from '@adobe/gatsby-theme-aio/src/components/Hero';
import classNames from "classnames";
import '@spectrum-css/typography';
import '@spectrum-css/card';
import {  TABLET_SCREEN_WIDTH,MOBILE_SCREEN_WIDTH, DESKTOP_SCREEN_WIDTH } from '@adobe/gatsby-theme-aio/src/utils';

import PropTypes from 'prop-types';

const counter = {
  2: 0,
  3: 0,
  4: 0
};
const alignMapping = ['flex-start', 'flex-end'];

const ProductCard = ({ theme = 'lightest', width = '100%', icon, heading, text, buttons, className }) => {
  let initColumns = 100 / parseFloat(width);

  if (width === '33%') {
    width = `${(100 / 3).toFixed(2)}%`;
    initColumns = 3;
  }

  const [columns] = useState(initColumns);

  useEffect(() => {
    return () => {
      if (typeof counter[columns] !== 'undefined') {
        counter[columns]--;
      }
    };
  }, [columns]);

  if (typeof counter[columns] !== 'undefined') {
    counter[columns]++;
  }

  let alignment = 'center';
  if (columns === 2 || columns === 3) {
    alignment = alignMapping[counter[columns] % columns] || 'center';
  }
  const MIN_MOBILE_SCREEN_WIDTH = '320px'
  return (
    <section
      className={classNames(className, `spectrum--${theme}`)}
      css={css`
        display: inline-flex;
        flex-direction: column;
        align-items: ${alignment};
        width: ${width};
        padding: var(--spectrum-global-dimension-size-400) 0;
        background: var(--spectrum-global-color-gray-100);

        @media screen and (min-width: ${MIN_MOBILE_SCREEN_WIDTH}) and (max-width: ${MOBILE_SCREEN_WIDTH})  {
          display: flex;
          width: 100%;
          align-items: center;
        }

      `}>
      <div
        role="figure"
        // tabIndex="0"
        className="spectrum-Card"
        css={css`
          margin: 0 var(--spectrum-global-dimension-size-300);
          width: calc(var(--spectrum-global-dimension-size-4600) - var(--spectrum-global-dimension-size-800));
          height: calc(var(--spectrum-global-dimension-size-4600) - var(--spectrum-global-dimension-size-500));

          &:hover {
            border-color: var(--spectrum-card-border-color, var(--spectrum-global-color-gray-200));
          }

          @media screen and (max-width: ${DESKTOP_SCREEN_WIDTH}) {
            width: calc(var(--spectrum-global-dimension-size-3600) - var(--spectrum-global-dimension-size-800));
          }

          @media screen and (max-width: ${TABLET_SCREEN_WIDTH}) {
            width: 0;
            margin: 0;
          }
        `}>
        <div
          className="spectrum-Card-body"
          css={css`
            height: calc(var(--spectrum-global-dimension-size-4600) - var(--spectrum-global-dimension-size-500));
            overflow: auto;
            text-align: left;
          `}>
          <div
            css={css`
              position: absolute;
              height: var(--spectrum-global-dimension-size-800);
              z-index: 1;
            `}>
            {icon &&
              cloneElement(icon, {
                css: css`
                  height: var(--spectrum-global-dimension-size-600);
                  width: var(--spectrum-global-dimension-size-600);
                  margin-top: 0;

                  img {
                    display: block;
                    height: 100%;
                    object-fit: contain;
                  }
                `
              })}
          </div>
          <div
            css={css`
              position: relative;
              z-index: 1;
              background-color: var(--spectrum-global-color-gray-50);
              ${icon ? 'top: var(--spectrum-global-dimension-size-800);' : ''}
            `}>
            <div
              className="spectrum-Card-header spectrum-Heading spectrum-Heading--sizeXXS"
              css={css`
                margin-top: 0 !important;
                margin-bottom: var(--spectrum-global-dimension-size-100) !important;
              `}>
              <div
                className="spectrum-Card-title"
                css={css`
                  font-size: var(--spectrum-global-dimension-size-200);
                `}>
                <strong>{heading && heading.props.children}</strong>
              </div>
            </div>
            <div
              className="spectrum-Card-content spectrum-Body spectrum-Body--sizeS"
              css={css`
                height: auto;
                margin-bottom: 0 !important;
              `}>
              {text && text.props.children}
            </div>
          </div>
        </div>
        <div className="spectrum-Card-footer">
          <HeroButtons
            buttons={buttons}
            quiets={[true, false]}
            variants={['secondary', 'primary']}
            css={css`
              justify-content: flex-end;

              @media screen and (max-width: ${TABLET_SCREEN_WIDTH}) {
                justify-content: center;
              }
            `}
          />
        </div>
      </div>
    </section>
  );
};

ProductCard.propTypes = {
  theme: PropTypes.string,
  width: PropTypes.oneOf(['100%', '50%', '33%', '25%']),
  icon: PropTypes.element,
  heading: PropTypes.element,
  text: PropTypes.element,
  buttons: PropTypes.element,
  className: PropTypes.string
};

export { ProductCard };
